@import './common.scss';

body {
  height: 100vh;
  background: url(../assets/images/bg.jpg) no-repeat center/cover fixed;
}

a{
  color: inherit; text-decoration: none;
}

.container {
  padding: 50px 0;
  .heros {
    @include center();
    .hero {
      width: 80px;
      height: 84px;
      margin: 4px;
      border: 3px solid #fff;
      border-radius: 10px;
      box-sizing: border-box;
      overflow: hidden;
      background-color: #555;
      transform: skewX(-14deg);
      transition: .1s, background-color .6s;
      &:hover {
        background-color: #ff9c00;
        transform: skewX(-14deg) scale(1.3);
        z-index: 1;
      }
      .image {
        width: 140%;
        height: 100%;
        transform: translateX(-16px) skewX(14deg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: 90px;
      }
    }
  }
  .logo {
    max-width: 300px;
    margin: 0 auto;
    padding: 0 20px;
    img {
      width: 100%;
    }
    p {
      text-align: center;
      font-size: 12px;
    }
  }
}